const INLINE_ATTRIBUTION_REGEX = /:ref\[[\s\S]+\]/
const INLINE_ATTRIBUTION_LIST_REGEX = /:ref\[LIST:[\s\S]+\]/
const DATA_FROM_REF_REGEX = /:ref\[(\S+):(\S+)\]/
const LIST_DATA_FROM_REF_REGEX = /:ref\[LIST:(\[(\S+):(\S+)\]+)\]/
const SINGLE_ITEM_REGEX = /\[([^\]]+)\]/g
const INLINE_ATTRIBUTION_REGEX_FOR_REMOVAL = /`:ref\[[\S]+\]`/g

export {
  INLINE_ATTRIBUTION_REGEX,
  INLINE_ATTRIBUTION_LIST_REGEX,
  DATA_FROM_REF_REGEX,
  LIST_DATA_FROM_REF_REGEX,
  SINGLE_ITEM_REGEX,
  INLINE_ATTRIBUTION_REGEX_FOR_REMOVAL,
}
